import App from "../../App.vue"
import {
	uniPopup
} from '@dcloudio/uni-ui'
export default {
	components: {
		uniPopup
	},
	data() {
		return {
			tempThumbPath: [],
			tempVideoPath: [],
			result: {},
			src: "",
			alpha: "",
			b: "",
			c: "",
			disabled: false,
			ctx: {},
			currentVideo: 0,
			videoContext: {},
			motion: ["点头视频", "张嘴视频", "眨眼视频", "摇头视频", "保持不动视频"],
			number: "",
			user: "",
			schoolList: ["华南农业大学", "华南理工大学", "暨南大学"],
			school: "华南农业大学",
			index: 0
		}
	},
	onLoad() {
		var that = this
		that.ctx = uni.createCameraContext()
		that.videoContext = uni.createVideoContext('myVideo')
		console.log(that.videoContext)
		//that.detect()
	},
	onShow() {
		var that = this
		//that.detect()
	},
	methods: {
		bindPickerChange: function(e) {
			var that = this
			console.log(e)
			console.log('picker发送选择改变，携带值为', e.target.value)
			var ind = e.target.value
			that.index = ind
			that.school = that.schoolList[ind]
		},
		yqz: function() {
			var that = this
			uni.stopAccelerometer()
			uni.navigateTo({
				url: "camera"
			})
		},
		error(e) {
			console.log(e.detail)
		},
		set_tempVideoPath: function(temp) {
			var that = this
			that.tempVideoPath = JSON.parse(temp)
			console.log(that.tempVideoPath[4])
		},
		changeVideo: function() {
			var that = this
			if (that.currentVideo < that.tempVideoPath.length) {
				that.currentVideo++
			} else {
				that.currentVideo = 0
			}
			console.log(that.currentVideo)
			that.videoContext.autoplay = 'true' //设置自动播放
			setTimeout(() => {
				that.videoContext.play() //播放视频
			}, 1000)
		},
		detect: function() {
			var that = this
			console.log("fuxk")
			uni.onAccelerometerChange(function(res) {
				console.log("1233")
				that.alpha = parseInt(Math.abs(res.x * 10))
				that.b = parseInt(Math.abs(res.y * 10))
				that.c = parseInt(Math.abs(res.z * 10))
				if (that.alpha >= 9 && that.b <= 2 && that.c <= 2) {
					uni.showToast({
						title: "可以开始录制视频",
						icon: "none"
					})
					that.disabled = false
				} else {
					uni.showToast({
						title: "检测到手机未横屏",
						icon: "none"
					})
					that.disabled = true
				}
			})
		},
		upload: function() {
			var that = this
			console.log(that.user)
			console.log(that.number)
			console.log(that.school)
			console.log(that.tempVideoPath[0])
			if (that.user.length > 0 && that.number.length > 0) {
				uni.showLoading({
					title: "正在上传"
				})
				for (let i = 0; i < 1; i++) {
					uni.uploadFile({
						url: "http://localhost:54473/api/Values/postFiles?userName=" + that.user +
							"&type=" + that.motion[i] + "&school=" + that.school, //服务器接口
						method: 'POST', //这句话好像可以不用
						filePath: that.tempVideoPath[i],
						header: {
							"Content-Type": "multipart/form-data"
						},
						name: 'file', //服务器定义的Key值
						success: function(res) {
							console.log(res)
							if (i == 0) {
								uni.hideLoading()
								that.$refs.popup.close()
								uni.showToast({
									title: "上传完成"
								})
							}
						},
						fail: function(res) {
							console.log(res)
							uni.showToast({
								title: "视频上传失败",
								icon: "none"
							})
						}
					})
				}
			} else {
				uni.showToast({
					title: "请输入姓名或学号",
					icon: "none"
				})
			}
		},
		restart: function() {
			var that = this
			that.tempVideoPath = []
			uni.showToast({
				title: "已重置视频,请重新录像"
			})
		},
		open: function() {
			var that = this
			this.$refs.popup.open()
		}
	}
}
